
import axios from 'axios';
import {HOST_API} from '../../config'

export async function  getUserList (skip,limit,search,from,to) {
    try { 
      const response = await axios.post(`${HOST_API}customers/customers-list`,{'skip':skip,'limit':limit,'search':search,from,to});
     return response.data.response
      // if(response.data.status !==0){
      //   dispatch(slice.actions.getUsersSuccess(response.data.response.data));
      // }else{
      //   dispatch(slice.actions.hasError(response.data.invoiceId));
      // }
    } catch (error) {
        return []
      
    }
   
  }
  
  export async function getUsersId (id) { 
    try { 
        console.log(id);
        const response =await axios.post(`${HOST_API}customer/customerDetails`,{_id:id});
        return response.data.response
      } catch (error) {
        return []
        
      }
  }
  
  export async function saveUser (_id,paidStatus) { 
    try { 
      const response =await axios.post(`${HOST_API}invoice/save`,{paidStatus,_id});
      return response.data.response
    } catch (error) {
        return []
    //   dispatch(slice.actions.hasError(error));
    }
  }
  